import React, { Component } from 'react';
import axios from 'axios'
import './css/App.css';
import Movie from './Movie';

class App extends Component {
  state = {
    isLoading : true,
    movies:[], //영화데이터가 로딩 되는 곳
  };

  getMovies = async () => {
    const {data : {
      data : {movies},
      },
    } = await axios.get('https://yts.mx/api/v2/list_movies.json?sort_by=rating');
    console.log(movies); //영화데이터 20개만 간소화해서 불러오기
    // this.setState({movies:movies});  //movies는 state이고 뒤 movies는 객체명
    this.setState({movies,isLoading : false}); // state명과 객체명이 같으면 es6문법에 따라 생략이 가능해 한번만 작성해주면 된다.
  }

  componentDidMount(){
    this.getMovies();
    // setTimeout(()=>{
    //   this.setState({isLoading:false});
    // },6000)
  }
  render() {
    const {isLoading,movies} = this.state
    return (
      <>
        <div className='intro'>{isLoading
        ?
        <img src={`${process.env.PUBLIC_URL}/img/iPhone.png`} alt='dfd' />
        :
        movies.map((movie)=>(
          <Movie key={movie.id} year={movie.year} title={movie.title} summary={movie.summary} poster={movie.medium_cover_image} genres={movie.genres} />
        ))}
        </div>
        {/* <h2>영화 데이터 서비스</h2>
        <p>로딩문구 만들기</p>
        <p>삼항조건연산자 조건식?참인값:거짓인값</p> */}
      </>
    );
  }
}

export default App;